<script setup>
    import { ref } from 'vue';
    import { useRouter } from 'vue-router';
    import post from '../../utils/post';

    const router = useRouter();
    
    const name = ref('');
    const lastName = ref('');

    const selectedGenre = ref('');

    async function createCharacter(){
        if(name.value.trim() === '' || name.value.length > 20) return;
        if(lastName.value.trim() === '' || lastName.value.length > 20) return;
        if(selectedGenre.value.trim() === '') return;
        let sex = '';
        if(selectedGenre.value === 'M') sex = 'mp_m_freemode_01';
        if(selectedGenre.value === 'F') sex = 'mp_f_freemode_01';
        post('NewCharacter', { name: name.value, lastname: lastName.value, sex });
    }

    function goToSelection(){
        router.push('/selection');
    }
</script>

<template>
    <div id="create">
        <div id="container-create">
            <div class="background-one-create"></div>
            <div class="content-container-create">
                <div class="header-container-create">
                    <p>CRIAÇÃO</p>
                    <p>DE PERSONAGEM</p>
                </div>
                <div class="infos-container-create">
                    <div class="content-infos-container-create">
                        <p>NOME</p>
                        <div class="infos-frame-create">
                            <div class="icon-infos-frame-create">
                                <img src="./images/user.svg" />
                            </div>
                            <input v-model="name" type="text" placeholder="Digite o nome" />
                        </div>
                    </div>
                    <div class="content-infos-container-create">
                        <p>SOBRENOME</p>
                        <div class="infos-frame-create">
                            <div class="icon-infos-frame-create">
                                <img src="./images/user.svg" />
                            </div>
                            <input v-model="lastName" type="text" placeholder="Digite o sobrenome" />
                        </div>
                    </div>
                    <div class="content-infos-container-create">
                        <p>GÊNERO</p>
                        <div class="infos-frame-create">
                            <div :class="{'genre-active' : selectedGenre == 'M'}" class="male" @click="selectedGenre = 'M'">Masculino</div>
                            <div :class="{'genre-active' : selectedGenre == 'F'}" class="female" @click="selectedGenre = 'F'">Feminino</div>
                        </div>
                    </div>
                </div>
                <button @click="createCharacter">CRIAR</button>
                <p class="text-bottom-create">Já tem um personagem ? <span @click="goToSelection">Selecionar</span></p>
            </div>
        </div>
    </div>
</template>

